import React from 'react';
import ReactDOM from 'react-dom';
import './tic.css';



  function Square(props){
    return(
      <button className="square" onClick={props.onClick}>
        {props.value}
      </button>
    )
  }
  
  class Board extends React.Component {
    constructor(props){
      super(props)
      this.state = {
        "squares": Array(9).fill(null),
        "xIsNext": true
      }
    }

    handleClick (i){


      const squares = this.state.squares.slice()
      if (!calculateWinner(squares) && squares[i] == null )
      {
        squares[i] = this.state.xIsNext ? "X":"O"
        this.setState({
          "squares" : squares,
          "xIsNext" : !this.state.xIsNext
        })
      }
    }
    
    renderSquare(i) {
      return <Square value={this.state.squares[i]} onClick={() => this.handleClick(i)} />;
    }

    restartBoard(){
      console.log("In New Method")

      this.setState({
        "squares": Array(9).fill(null),
        "xIsNext": true
      })
    }
  
    render() {
       var winner = calculateWinner(this.state.squares)
       console.log(winner)
       var status
       if (winner)
       {
         status = "Winner: " + winner
       }
       else{
       status = "Next player: " + (this.state.xIsNext ? "X" :"O") ;
       }
      return (
        <div>
          <div className="status">{status}</div>
          <div className="board-row">
            {this.renderSquare(0)}
            {this.renderSquare(1)}
            {this.renderSquare(2)}
          </div>
          <div className="board-row">
            {this.renderSquare(3)}
            {this.renderSquare(4)}
            {this.renderSquare(5)}
          </div>
          <div className="board-row">
            {this.renderSquare(6)}
            {this.renderSquare(7)}
            {this.renderSquare(8)}
          </div>
          <button onClick={()=>this.restartBoard()}>Restart Game</button>
        </div>
      );
    }
  }
  
  class Game extends React.Component{

    constructor(props){
      super()

      this.state = {
        "restart" : false,
        "initialSetup": true
      }
    }






    render() {
      return (
        <div className="game">
          <div className="game-board">
            <h2>Fancy a Game of Tic-Tac-Toe With a friend?</h2>
            <h2>Your in the right place (Only two player supported at this time)</h2>
            <Board></Board>
          </div>
          <div className="game-info">
            <div>
            </div>
            <ol>{}</ol>
          </div>
        </div>
      );
    }
  }

  
  // ========================================
  
  ReactDOM.render(

    <Game />,
    document.getElementById('root')
  );
  
  function calculateWinner(squares) {
    const lines = [
      [0, 1, 2],
      [3, 4, 5],
      [6, 7, 8],
      [0, 3, 6],
      [1, 4, 7],
      [2, 5, 8],
      [0, 4, 8],
      [2, 4, 6],
    ];
    for (let i = 0; i < lines.length; i++) {
      const [a, b, c] = lines[i];
      if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
        return squares[a];
      }
    }
    return null;
  }

  export default Game
  